
[class^=icon-],[class*=" icon-"]{font-size:24px
}
#header-holder{height:59px
}
#header-holder .navbar{position:relative;height:58px;line-height:58px;border-bottom:1px solid transparent;background:rgba(255,255,255,0.97);z-index:500
}
#header-holder .navbar .navbar-logo-container{position:absolute;top:0;left:0;width:58px;height:58px;z-index:1
}
#header-holder .navbar .navbar-logo-container .logo{display:inline-block;width:100%;height:100%;position:absolute;top:0;left:0;line-height:58px;text-align:center;color:gray;text-decoration:none;transition:all .05s ease-in-out;font-weight:bold;font-family:"楷体"
}
#header-holder .navbar .navbar-title-container{position:absolute;width:660px;left:50%;margin-left:-330px;color:#414243;line-height:1;padding-top:10px;opacity:0;visibility:hidden
}
#header-holder .navbar .navbar-title-container>div{float:left
}
#header-holder .navbar .navbar-title-container .z-image-wraper{margin-right:12px
}
#header-holder .navbar .navbar-title-container .titles.oneline{line-height:34px
}
#header-holder .navbar .navbar-title-container .titles.oneline .main-title{font-size:16px;padding-top:3px;overflow:hidden;text-overflow:ellipsis;font-weight:700;white-space:nowrap
}
#header-holder .navbar .navbar-title-container .functions{margin-left:32px;padding-top:3px
}
#header-holder .navbar .navbar-title-container .functions .z-btn{width:80px;height:32px;padding:0;min-height:0;min-width:0;line-height:30px;box-sizing:border-box;border-color:#50C87E;color:#50C87E;border:1px solid;border-radius:4px;margin:0;cursor:pointer;font-size:14px;background-color:transparent;transition:background ease-out .1s
}
#header-holder .navbar .navbar-title-container.show{opacity:1;visibility:visible
}
#header-holder .navbar .navbar-title-container.anim{transition:all .2s ease-out
}
#header-holder .navbar .navbar-write-container{position:relative;float:right;padding:0 10px
}
#header-holder .navbar .navbar-write-container>a{height:54px;top:0;left:0;color:gray;text-decoration:none;display:inline-block
}
#header-holder .navbar .navbar-write-container>a i{margin-right:3px
}
#header-holder .navbar-fixed{border-bottom-color:rgba(0,0,0,0.08);position:fixed;left:0;right:0;top:-59px;transition:all .2s ease-out
}
#header-holder .navbar-fixed-show{top:0
}


